import React, { useState, useEffect } from "react";
import io from "socket.io-client";
import "./index.css";

let socket;
const CONNECTION_PORT = 'wss://tarea-3-websocket.2021-1.tallerdeintegracion.cl/flights'

function Chat() {
  // Before Login
  const [name, setUserName] = useState("");

  // After Login
  const [message, setMessage] = useState("");
  const [messageList, setMessageList] = useState([]);

  useEffect(() => {
    socket = io(CONNECTION_PORT);
  }, [CONNECTION_PORT]);

  useEffect(() => {
    socket.on("CHAT", (data) => {
      setMessageList([...messageList, data]);
    });
  });

  const sendMessage = async () => {
    let messageContent = {name: name, message: message};

    await socket.emit("CHAT", messageContent);
    setMessageList([...messageList, messageContent]);
    setMessage("");
  };

  return (
    <div>
        <div>
          <div>
            <input
              type="text"
              placeholder="Name..."
              onChange={(e) => {
                setUserName(e.target.value);
              }}
            />
          </div>
        </div>
        <div>
          <div>
            {messageList.map((val, key) => {
              return (
                <div>
                  <div>
                    {val.name}:{val.date} {val.message}
                  </div>
                </div>
              );
            })}
          </div>
          <div>
            <input
              type="text"
              placeholder="Message..."
              onChange={(e) => {
                setMessage(e.target.value);
              }}
            />
            <button onClick={sendMessage}>Send</button>
          </div>
        </div>
    </div>
  );
}

export default Chat;